.container {
  font-family: "Lato", "Helvetica Neue", Helvetica, Arial, sans-serif;
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 2.5em 1em;
  column-gap: 9em;
}
@media only screen and (max-width: 768px) {
  .container {
    flex-direction: column;
    padding: 1.55em 1em;
    column-gap: 0;
  }
}

.title {
  font-size: 1.75em;
  font-weight: bold;
  color: #373742;
  text-transform: uppercase;
  text-align: center;
}

.subTitle {
  font-size: 1.25em;
  color: #707070;
  padding-top: 0.75em;
  text-align: center;
}

.value {
  display: flex;
  justify-content: center;
  font-size: 4em;
  font-weight: bold;
  color: #373742;
}